/* src/App.css */
body {
  background-color: #1e1e1e;
  color: #fff;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  overflow: hidden;
}

.App {
  text-align: center;
  width: 100%;
  max-width: 800px;
  height: 100%;
  overflow-y: auto;
  padding: 20px;
  box-sizing: border-box;
}

.character-creation,
.boss-fight {
  background: #333;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  margin: 10px 0;
  width: 100%;
  box-sizing: border-box;
}

input, select, button {
  margin: 10px 0;
  padding: 10px;
  border-radius: 5px;
  border: none;
  font-size: 1em;
  width: calc(100% - 20px);
  box-sizing: border-box;
}

button {
  background-color: #ffcc00;
  color: #000;
  cursor: pointer;
}

button:hover {
  background-color: #ffaa00;
}

ul {
  list-style: none;
  padding: 0;
  width: 100%;
}

li {
  margin: 10px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  box-sizing: border-box;
}

img {
  max-width: 100%;
  max-height: 300px;
  height: auto;
  border: 2px solid #ffcc00;
  border-radius: 10px;
  margin: 20px 0;
}

.highlight {
  background-color: #ffcc00;
  color: #000;
}

.add-chore {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.add-chore input {
  margin: 5px 0;
}

.add-chore label {
  display: flex;
  align-items: center;
  margin: 5px 0;
}

.health-bar {
  background: #ff6666;
  border-radius: 10px;
  overflow: hidden;
  height: 20px;
  margin: 20px 0;
}

.health {
  background: #66ff66;
  height: 100%;
}

.error {
  color: red;
  margin: 10px 0;
}

.boss-image {
  max-height: 300px;
  object-fit: cover;
}

.dice-container {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  height: 150px;
}

.die {
  width: 100px;
  height: 100px;
  transform-style: preserve-3d;
  transform: rotateX(-30deg) rotateY(-45deg);
  transition: transform 1s;
}

.die.rolling {
  animation: roll 1s infinite;
}

@keyframes roll {
  0% { transform: rotateX(0deg) rotateY(0deg); }
  100% { transform: rotateX(360deg) rotateY(360deg); }
}

.die .face {
  position: absolute;
  width: 100px;
  height: 100px;
  background: #ffcc00;
  border: 1px solid #000;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  font-weight: bold;
}

.die .face-1 { transform: rotateY(0deg) translateZ(50px); }
.die .face-2 { transform: rotateY(90deg) translateZ(50px); }
.die .face-3 { transform: rotateY(180deg) translateZ(50px); }
.die .face-4 { transform: rotateY(-90deg) translateZ(50px); }
.die .face-5 { transform: rotateX(90deg) translateZ(50px); }
.die .face-6 { transform: rotateX(-90deg) translateZ(50px); }
.die .face-7 { transform: rotateY(45deg) translateZ(50px); }
.die .face-8 { transform: rotateY(135deg) translateZ(50px); }
.die .face-9 { transform: rotateY(225deg) translateZ(50px); }
.die .face-10 { transform: rotateY(315deg) translateZ(50px); }
.die .face-11 { transform: rotateX(45deg) translateZ(50px); }
.die .face-12 { transform: rotateX(-45deg) translateZ(50px); }
.die .face-13 { transform: rotateY(60deg) translateZ(50px); }
.die .face-14 { transform: rotateY(-60deg) translateZ(50px); }
.die .face-15 { transform: rotateX(60deg) translateZ(50px); }
.die .face-16 { transform: rotateX(-60deg) translateZ(50px); }
.die .face-17 { transform: rotateY(75deg) translateZ(50px); }
.die .face-18 { transform: rotateY(-75deg) translateZ(50px); }
.die .face-19 { transform: rotateX(75deg) translateZ(50px); }
.die .face-20 { transform: rotateX(-75deg) translateZ(50px); }
